import * as React from 'react';
import hd from "./Header.module.css"
import logo from "./images/logo.png"

const Header=(props:any)=>{

    return(<div className={hd.HeaderBody}>

        <div className={hd.navBar}>
            <img src={logo} className={hd.logo}></img>
            <div className={hd.navBarMain}>

            <a href={"#"}>ТБ</a>
            <a href={"#"}>Фільми</a>
            <a href={"#"}>Прем'єри</a>
            <a href={"#"}>Тарифи</a>
            <a href={"#"}>Як підключити</a>
            <a href={"#"}>Промокод</a>
            <a href={"#"}>Ще</a>
            </div>
        <div className={hd.navBarOther}>
            <a>F</a>
            <a>The</a>
            <a>UKR</a>
            <a>__Увійти</a>
        </div>
        </div>


</div>)

}

export default Header;