import React, {useEffect, useState} from 'react';

import './App.css';

import {api} from "./api";
import Movie from "./Movie";
import Header from "./Header";


function App() {
    const [movies, setMovies] = useState([])

    useEffect(() => {

        api.getFilms().then((data) => {
            return data.data.results
        }).then((result) => {
            setMovies(result);
        })
    }, [])

    return (<div className={"innerBody"}>
        <Header/>
        <div className="App">
            {movies.length > 0 ? movies.map((u) => <Movie key={u.id} film={u}/>) : <div>PRELOADER</div>}
        </div>
    </div>);
}

export default App;
