import * as React from 'react';
import { api } from './api';
import "./Movie.css"
const API_IMAGES="https://image.tmdb.org/t/p/w1280";
const Movie=(props:any)=>{

    return(<div className={"movie"} id={"movieID"}>
        {props.film.poster_path!=null?
<div>
    <div className={"divTR"} >    <img className={"transf"} src={API_IMAGES+props.film.poster_path}></img>
    </div>
            <div className={"description"}>
            <h4 className={"title"}>{props.film.original_title.length<20?props.film.original_title:props.film.original_title.slice(0,20)+"..."}</h4>
            <h5 className={"genre"}>Horror,Comedy</h5>
            </div>
</div> :null}





</div>)

}

export default Movie